<template>
<div class="identifier">
 <div style="position:absolute;top:2px;right:5px;" class="fa fa-close" @click="remove"></div>
 <div>
   <label>Autorité</label>
   <span v-if="['Personal', 'Organizational'].indexOf(type) >= 0">
      <select v-model="meta.type">
       <option v-for="tp in types" :value="tp">{{tp}}</option>
     </select>
   </span>
   <span v-else>
	   <input v-model="meta.type" list="types" type="text" required placeholder="local identification, URN, URL ou ..."
	   class="medium" />
	   <datalist id="types">
	     <option v-for="tp in types" :value="tp">{{tp}}</option>
	   </datalist>
   </span>
   <meta-mro value="M"></meta-mro>
  </div>
 <div>
  <label> Identifiant </label>
  <input type="text" v-model="meta.identifier" required class="medium" @change="change" >
   <meta-mro value="M"></meta-mro>
  </div>
</div>
</template>
<script>
 import MetaMro from './metadata-mro.vue'
 export default {
   name: 'MetadataIdentifier',
   components: {
     MetaMro
   },
   props: {
     id: {
       type: Number,
       default: -1
     },
     identifier: {
       type: Object,
       default: () => {}
     },
     type: {
       type: String,
       default: 'article'
     }
   },
   watch: {
     type (newvalue) {
       this.initialize(newvalue)
     },
     identifier (newvalue) {
       this.meta = newvalue
     }
   },
   data () {
     return {
       meta: {
         type: null,
         identifier: null
       },
       types: ['URI', 'URN', 'URL']
     }
   },
   created () {
     this.meta = this.identifier
     this.initialize(this.type)
   },
   methods: {
     change () {
       this.$emit('change', {id: this.id, identifier: this.meta})
     },
     initialize (type) {
       switch (type) {
       case 'Personal': 
         this.types = ['ISNI', 'ORCID']
         this.meta.type = 'ORCID'
         break
       case 'Organizational':
         this.types = ['ISNI', 'ORCID', 'GRID', 'ROR']
         this.meta.type = 'ROR'
         break
       case 'article':
         this.types = [null, 'URI', 'URN']
         break
       }
     },
     remove () {
       this.$emit('remove', this.id)
     }
   }
 }
</script>
<style scoped>
label {
  font-size:1em;
  min-width:120px;
  max-width:120px;
}
div.identifier {
  position: relative;
  padding: 5px 10px;
  border: 1px solid grey;
  border-radius: 5px;
  background: #F5F5F5;
  margin:8px 5px 8px 0;
}
div.contact div.identifier {
  background: white;
}
</style>